<template class="Form chính xuất hàng theo đầu đơn xin cấp phát thuốc">
    <div class="form-input-receive-doc-num" v-if="isShowReceiveDocNum">
        <el-form label-width="auto" style="max-width: 500px">
            <el-form-item :label="$t('Mã đầu đơn xin cấp phát thuốc')">
                <el-input v-model="frm.receiveDocNum" :placeholder="$t('Nhập vào mã đầu đơn xin cấp phát thuốc')"
                    clearable />
            </el-form-item>
            <el-form-item>
                <div>
                    <el-row :gutter="24" justify="center">
                        <el-col :md="8">
                        </el-col>
                        <el-col :md="8">
                            <el-button size="large" type="success" plain @click="CheckReceiveDocNum()">
                                <el-icon>
                                    <Pointer />
                                </el-icon>
                                &nbsp;{{ $t('Kiểm tra') }}</el-button>
                        </el-col>
                    </el-row>

                </div>
            </el-form-item>
        </el-form>
    </div>
    <div class="form-main" v-else>
        <div style="text-align:left">
            <el-button style="border:none" @click="ResetSomeThing()">
                <el-icon>
                    <Back />
                </el-icon>
                &nbsp; {{ $t('Trở lại') }}
            </el-button>
        </div>

        <div class="form thao tác xuất hàng">
            <h3 style="text-align: left;">{{ $t('Thao tác xuất hàng:') }}</h3>
            <el-divider />
            <el-row :gutter="24" justify="start">
                    <el-col :md="10">
                        <el-form-item :label="$t('Mã đầu đơn xin cấp phát thuốc')">
                            <el-input v-model="appDisMedicineInfor.AppDisMedince.F_RECEIVE_DOC_NUM" readonly />
                        </el-form-item>
                    </el-col>
             </el-row>
             <el-row :gutter="24" justify="start">
                    <el-col :md="6">
                        <el-form-item :label="$t('Mã thẻ người nhận')"  :rules="[{ required: true },]">
                            <el-input v-model="curEmpReceive" clearable />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Khu xưởng/Phòng y tế')">
                            <el-input v-model="appDisMedicineInfor.AppDisMedince.F_FACTORYZONE" readonly />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Đơn vị lĩnh dùng')">
                            <el-input v-model="appDisMedicineInfor.AppDisMedince.F_RECEIVE_UNIT" readonly />
                        </el-form-item>
                    </el-col>
                 </el-row>


                 <el-row :gutter="24" justify="start">
                    <el-col :md="6">
                        <el-form-item :label="$t('Số hiệu hàng xuất')"  :rules="[{ required: true },]">
                            <el-select @change="ChangeMedicine()" v-model="curMedinceId"
                                :placeholder="$t('Vui lòng chọn số hiệu')" clearable>
                                <el-option v-for="item in appDisMedicineInfor.AppDisMedinceDetailLs" :key="item.value"
                                    :label="item.F_MEDICINE_ID" :value="item.F_MEDICINE_ID" />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Tên hàng xuất')">
                            <el-input v-model="curMedicineName" readonly />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Hạn sử dụng')"  :rules="[{ required: true },]">
                            <el-date-picker v-model="curExpiredDate" clearable type="date" format="YYYY/MM/DD"
                                value-format="YYYY/MM/DD" :placeholder="$t('Chọn hạn sử dụng')" />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Quy cách')">
                            <el-input v-model="curMedicineObjUnit" readonly />
                        </el-form-item>
                    </el-col>
                   
                </el-row>
                <el-row :gutter="24" justify="start">
                    <el-col :md="6">
                        <el-form-item :label="$t('Ngày xuất thực tế')"  :rules="[{ required: true },]">
                            <el-date-picker v-model="curRealOutPutDate" clearable type="date" format="YYYY/MM/DD"
                                value-format="YYYY/MM/DD" :placeholder="$t('Chọn ngày xuất thực tế')" />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Ghi chú')">
                            <el-input v-model="curNote"  />
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24" justify="start">
                    <el-col :md="6">
                        <el-form-item :label="$t('Số lượng tồn hiện tại')">
                            <el-input v-model="curQtyInStockNow" readonly />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Số lượng tồn mới')">
                            <el-input v-model="curQtyInStockNew" readonly />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Số lượng cần xuất')">
                            <el-input v-model="curQtyNeedExportFollowDocNum" readonly />
                        </el-form-item>
                    </el-col>
                    <el-col :md="6">
                        <el-form-item :label="$t('Số lượng xuất thực tế')" :rules="[{ required: true },]">
                            <el-input-number  v-model="curQtyExportReal" @change="ChangeQtyExportReal()"
                            :placeholder="$t('Nhập vào số lượng')" clearable :min="0" :max="1000000000" :precision="0"
                            value-on-clear="min" />
                        </el-form-item>
                    </el-col>
                </el-row>
               
                <el-row :gutter="24">
                    <el-col :md="11">
                        </el-col>
                    <el-col :md="2">
                        <el-button size="large" type="success"  @click="AddTempMedicineExport()" plain>
                                <el-icon><Plus /></el-icon>
                                &nbsp;{{ $t('Thêm') }}</el-button>
                    </el-col>
                    <el-col :md="11">
                        </el-col>
                </el-row>
         
        </div>
        <div v-if="this.frm.medicineExportLs!=null &&  this.frm.medicineExportLs.length> 0 " 
            class="form show thông tin cac loại thuốc đã nhập số lượng xuất sau khi nhan nút Thêm">
            <h3 style="text-align: left;">{{ $t('Chi tiết các sản phẩm đã nhập số lượng và chuẩn bị xuất:') }}</h3>
            <el-divider />

            <el-table :data="this.frm.medicineExportLs"   style="width: 100%;height:100%;">
                <el-table-column type="index"  :label="$t('Số thứ tự')" />
                <el-table-column prop="F_MEDICINE_ID" :label="$t('Số hiệu')" />
                <el-table-column prop="F_MEDICINE_NAME" :label="$t('Tên hàng')"  />
                <el-table-column prop="F_MEDICINE_OBJ_UNIT" :label="$t('Quy cách')" />
                <el-table-column prop="F_EXPIRED_DATE" :label="$t('Hạn sử dụng')" />
                <el-table-column prop="F_QTY_REQUEST" :label="$t('Số lượng yêu cầu')" />
                <el-table-column prop="F_QTY_EXPORT_REAL" :label="$t('Số lượng xuất thực tế')" />
                <el-table-column prop="F_DATE_EXPORT_REAL" :label="$t('Ngày xuất thực tế')" />
                <el-table-column prop="F_EMP_RECEIVE" :label="$t('Người nhận')" />
                <el-table-column prop="F_NOTE" :label="$t('Ghi chú')" />
                <el-table-column :label="$t('Hành động')" >
                    <template #default="scope">
                            <el-button
                                size="small"
                                type="danger" plain
                                @click="DeleteTempMedicineExport(scope.row.F_MEDICINE_ID, scope.row.F_EXPIRED_DATE)"
                                >
                                <el-icon>
                                            <Delete />
                                        </el-icon>
                                &nbsp; {{ $t('Xóa') }}
                                </el-button
                            >
                        </template>
                 </el-table-column>
            </el-table>
                <el-row :gutter="24" justify="center" style="margin-top:10px">
                    <el-col :span="2">
                        <el-popconfirm :confirm-button-text="$t('Có')" :cancel-button-text="$t('Không')" :icon="InfoFilled"
                            icon-color="#626AEF" :title="$t('Bạn có chắc chắn muốn thao tác?')" @confirm="ExportGoodsFromWHByNormalType()"
                            @cancel="cancelEvent"> <template #reference>
                                
                                    <el-button type="primary" size="large" plain>
                                        <el-icon><Position /></el-icon>
                                        &nbsp;
                                        {{ $t('Xuất kho') }}
                                    </el-button>
                            </template>
                        </el-popconfirm>
                        </el-col>
                </el-row>
        </div>
        <div id="tbDisMedicineInfor" class="form hiển thị thông tin các loại thuốc trong đơn xin cấp phát">
            <h3 style="text-align: left;">{{ $t('Chi tiết đơn xin cấp phát thuốc:') }}</h3>
            <el-divider />
            <el-table :data="this.appDisMedicineInfor?.AppDisMedinceDetailLs"   style="width: 100%">
                <el-table-column type="index"  :label="$t('Số thứ tự')" />
                <el-table-column prop="F_MEDICINE_ID" :label="$t('Số hiệu')" />
                <el-table-column prop="F_MEDICINE_NAME" :label="$t('Tên hàng')"  />
                <el-table-column prop="F_MEDICINE_OBJ_UNIT" :label="$t('Quy cách')" />
                <el-table-column prop="F_QTY_REQUEST" :label="$t('Số lượng yêu cầu')" />
                <el-table-column prop="F_REQUEST_DATE" :label="$t('Ngày yêu cầu')" />
                <el-table-column prop="F_QTY_RECEIVED" :label="$t('Số lượng đã nhận')" />
                <el-table-column prop="F_RECEIVED_DATE" :label="$t('Ngày nhận')" />
                <el-table-column prop="F_RECEIVED_EMP" :label="$t('Người nhận')" />
            </el-table>
        </div>
    </div>
</template>
<script>
import api from "@/model/api.js"
import { ElMessage } from 'element-plus'
import { ElMessageBox } from 'element-plus'
export default
    {
        name: "ProductDelivery",
        data() {
            return {
                isShowReceiveDocNum: true,
                frm: {
                    receiveDocNum: "",
                    medicineExportLs:[] , //danh sach cac loai thuoc sau khi nhan nut add
                },
                //temp infor:
                curMedinceId: "", //  MedinceId hien tai khi select option
                curMedicineObjUnit: "",// Quy cach thuoc hien tai.
                curQtyInStockNow: 0,// so luong ton kho hien tai
                curQtyInStockNew: 0,// so luong ton kho moi.
                curQtyNeedExportFollowDocNum: 0, // so luong can xuat theo dau don.
                curQtyExportReal: 0, //so luong xuat thuc te
                curRealOutPutDate: "", // Ngay xuat thuc te
                curExpiredDate: "",// Han su dung 
                curEmpReceive:"", // ma the nguoi nhan thuoc hien tai
                curNote:"",// Ghi chú cho phép note lại thông tin lý do số lượng xuất (nếu cần)
                curMedicineName:"", // ten thuoc
                //
                appDisMedicineInfor: null,  // luu thong tin chi tiet ve don linh dung get tu Api
                
            }
        },
        methods: {

            handleDelete(index,row)
            {
                console.log("aa"+index)
                console.log(row)
            } ,
            ExportGoodsFromWHByNormalType()
            {
                if(this.frm.receiveDocNum &&  this.frm.medicineExportLs.length>0)
                {
                   api.ExportGoodsFromWHByNormalType(
                    this.frm,
                    (dat)=>{
                        if (dat?.data) {
                                if (dat?.data?.status == "success") {

                                    ElMessageBox.alert(
                                        `<h3 style="color:green"> ${this.$t('Danh sách mã hiệu thuốc xuất thành công:')} </h3>
                                     <div style="background-color:green; color:white"> ${dat?.data?.data.successLs} </div>
                                     <h3 style="color:red"> ${this.$t('Danh sách mã hiệu thuốc xuất thất bại:')} </h3>
                                     <div style="background-color:red; color:white"> ${dat?.data?.data.errorLs} </div>
                                    `,
                                        this.$t('Thông báo'),
                                        {
                                            dangerouslyUseHTMLString: true,
                                            showClose: false
                                        }
                                    )
                                    this.frm.medicineExportLs=[];
                                    if(dat?.data?.data.successLs!="")
                                    {// neu co du lieu export success => refresh lai table  tbDisMedicineInfor
                                        this.CheckReceiveDocNumValidForExportNormal();
                                    }
                                }
                                else {  //error
                                    ElMessage.error(dat?.data?.message)
                                }
                            }

                    },
                    (er)=>{
                        ElMessage.error(er.message)
                    }
                   ); 
                }
            },
            ChangeQtyExportReal()
            {
                if(this.CheckValidCurObj())
                {
                    this.curQtyInStockNew=this.curQtyInStockNow -this.curQtyExportReal
                }
             
            },

            DeleteTempMedicineExport(curMedinceId,curExpiredDate)
            {
               let index= this.frm.medicineExportLs.findIndex(item => item.F_MEDICINE_ID === curMedinceId &&item.F_EXPIRED_DATE ==curExpiredDate );
               if(index!=-1)
               {
                this.frm.medicineExportLs.splice(index,1)
               }
            } ,
            CheckValidCurObj()
            {
                if(!this.curMedinceId.trim())
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t("Vui lòng chọn số hiệu thuốc"))
                    return false;
                }
                if(!this.curExpiredDate.trim())
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t("Vui lòng chọn hạn sử dụng"))
                    return false;
                }
                if(this.curQtyExportReal<=0)
                {
                    this.curQtyExportReal=0;
                    this.curQtyInStockNew= this.curQtyInStockNow
                    ElMessage.error(this.$t("Vui lòng nhập vào số lượng xuất thực tế > 0"))
                    return false;
                }

                if(this.curQtyInStockNow<=0)
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t("Số lượng tồn hiện tại phải > 0"))
                    return false;
                }
                if(!this.curRealOutPutDate.trim())
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t("Vui lòng nhập vào ngày xuất thực tế"))
                    return false;
                }

                if(this.curQtyExportReal > this.curQtyInStockNow)
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t('Số lượng xuất thực tế không được lớn hơn số lượng tồn kho hiện tại'))
                    return false;
                }
                if(this.curQtyExportReal > this.curQtyNeedExportFollowDocNum)  // // check soluong xuat thuc te > so luong nhu cau
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t('Số lượng xuất thực tế không được lớn hơn số lượng nhu cầu'))
                    return false;
                }

                if(!this.curEmpReceive.trim())
                {
                    this.curQtyExportReal=0;
                    ElMessage.error(this.$t("Vui lòng nhập mã thẻ người nhận"))
                    return false;
                }
              
                return true;
            }
            ,
            AddTempMedicineExport()
            {
                if(this.CheckValidCurObj()==false)
                 return
               
                let tempCurMedicine =this.appDisMedicineInfor?.AppDisMedinceDetailLs
                                     .filter((item) => { if (item.F_MEDICINE_ID == this.curMedinceId) return true; })[0];
                  
                if( this.frm.medicineExportLs.
                filter((item)=> {if(item.F_MEDICINE_ID==this.curMedinceId 
                    &&  item.F_EXPIRED_DATE == this.curExpiredDate) return true})
                 .length>0)
                 {
                    ElMessage.error(this.$t('Số hiệu thuốc và ngày hết hạn này đã được thêm vào danh sách chuẩn bị xuất, vui lòng kiểm tra lại'))
                    return
                 }

                tempCurMedicine.F_EXPIRED_DATE=  this.curExpiredDate;
                tempCurMedicine.F_QTY_EXPORT_REAL =  this.curQtyExportReal;
                tempCurMedicine.F_DATE_EXPORT_REAL = this.curRealOutPutDate;
                tempCurMedicine.F_EMP_RECEIVE   =this.curEmpReceive.trim().toUpperCase();
                tempCurMedicine.F_NOTE= this.curNote.trim()
                tempCurMedicine.F_MEDICINE_NAME= this.curMedicineName 
                tempCurMedicine.F_MEDICINE_OBJ_UNIT= this.curMedicineObjUnit

                let tempCurMedicine1 =  JSON.parse (JSON.stringify(tempCurMedicine))
                this.frm.medicineExportLs.push(tempCurMedicine1);
                this.ResetCurInfor();

            },
            ChangeMedicine() {
                if (this.curMedinceId) {
                    let tempCurMedicine =
                        this.appDisMedicineInfor?.AppDisMedinceDetailLs
                            .filter((item) => { if (item.F_MEDICINE_ID == this.curMedinceId) return true; })[0];
                    this.curMedicineObjUnit = tempCurMedicine.F_MEDICINE_OBJ_UNIT_VI + "/" + tempCurMedicine.F_MEDICINE_OBJ_UNIT_EN
                    this.curQtyNeedExportFollowDocNum = tempCurMedicine.F_QTY_REQUEST
                    this.curMedicineName=tempCurMedicine.F_MEDICINE_NAME_VI+"/"+tempCurMedicine.F_MEDICINE_NAME_EN

                }
                else { // no select
                    this.ResetCurInfor();
                }
            },
            ResetSomeThing() {
                this.ResetCurInfor()
                this.appDisMedicineInfor = null;
                this.isShowReceiveDocNum = true;

            },
            ResetCurInfor() {
                this.curMedicineObjUnit = "";
                this.curMedinceId = "";
                this.curQtyInStockNow = 0;// so luong ton kho hien tai
                this.curQtyInStockNew = 0;// so luong ton kho moi.
                this.curQtyNeedExportFollowDocNum = 0; // so luong can xuat theo dau don.
                this.curQtyExportReal = 0; //so luong xuat thuc te
                this.curRealOutPutDate = "";
                this.curExpiredDate = "";
                this.curEmpReceive="";
                this.curNote="";
                this.curMedicineName="";
            },
            CheckReceiveDocNum() {
               // this.ResetSomeThing();
               // this.CheckReceiveDocNumValidForExportNormal();
               
                this.$router.push({name:"HelloWorld", hash:"#" })
            },
            CheckReceiveDocNumValidForExportNormal()
            {
                api.CheckReceiveDocNumValidForExportNormal({ ReceiveDocNum: this.frm.receiveDocNum },
                    (dat) => {
                        if (dat.data.status == "error") {
                            ElMessage.error(dat?.data?.message)
                        }
                        else  //success
                        {
                            if (dat?.data?.data) {
                                this.isShowReceiveDocNum = false;
                                this.appDisMedicineInfor = dat?.data?.data;
                            }
                        }

                    },
                    (er) => {
                        ElMessage.error(er.message)
                    })
            } ,
            GetQtyInStockNow() {
                this.curQtyInStockNow = 0;
                this.curQtyInStockNew=0;
                if (this.curMedinceId && this.curExpiredDate &&
                    this.appDisMedicineInfor?.AppDisMedince?.F_FACTORYZONE
                ) {
                    api.GetTotalInOutInforOfAMedicine({
                            medicineId: this.curMedinceId,
                            medicineExpiredDate: this.curExpiredDate,
                            factoryZone: this.appDisMedicineInfor?.AppDisMedince?.F_FACTORYZONE
                        }, (dat) => {
                            if (dat?.data?.status == "success") {
                                if (dat?.data?.data) {
                                    if (isNaN(dat?.data?.data?.F_TOTAL_QTY) == false)  // is number
                                    {
                                        this.curQtyInStockNow = parseInt(dat?.data?.data?.F_TOTAL_QTY);
                                        this.curQtyInStockNew=this.curQtyInStockNow ;
                                    }
                                    else
                                    {
                                        this.curQtyInStockNow = 0;
                                        this.curQtyInStockNew = 0;
                                    }
                                     
                                }
                            }
                            else //error
                            {
                                ElMessage.error(dat?.data?.message)
                                return
                            }

                        }, (er) => {
                            ElMessage.error(er.message)
                        })
                }
            } ,
        },
        watch: {
            curMedinceId(newVal,oldVal){
                console.log(newVal,oldVal);
                this.GetQtyInStockNow();
            } ,
            curExpiredDate(newVal,oldVal){
                console.log(newVal,oldVal);
                this.GetQtyInStockNow();
            } ,
          

        },

    }
</script>
<style scoped>
table,
th,
td {
    padding: 10px;
    border: 1px solid black;
    border-collapse: collapse;
}
</style>